import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { css } from 'glamor' 

class Container extends Component {
  constructor(props) {
    super(props)
    this.state = {
      width: 992
    }
    this.setMaxWidth = this.setMaxWidth.bind(this)
  }

  componentDidMount() {
    this.setMaxWidth()
  }

  setMaxWidth() {
    if (this.props.xl) {
      this.setState({
        width: 1200
      })
    } else if (this.props.md) {
      this.setState({
        width: 768
      })
    } else {
      this.setState({
        width: 992
      })
    }
  }
  render() {
    const style = css({
      maxWidth: this.state.width,
      marginLeft: 'auto',
      marginRight: 'auto',
      paddingLeft: 16,
      paddingRight: 16,
      position: 'relative'
    })
    return (
      <div {...style}>
        {
          this.props.children
        }
      </div>
    )
  }
}

Container.propTypes = {
  md: PropTypes.string,
  xl: PropTypes.string
}

export default Container


